@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
:root {
  --blue-color: #00acdc;
  --white-color: #fff;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100dvw;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__links {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.link:not(:first-child) {
  margin-left: 20px;
}

.link__page {
  color: #000;
  text-transform: uppercase;
  place-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: grid;
  position: relative;
}

.link__page:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 2px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
